html {
  letter-spacing: normal;
  font-family: Roboto, sans-serif;
}

body {
  --header-height: 60px;
  --hover-color: #00acdc;
  margin: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.logo {
  width: 40px;
  height: 40px;
  margin-left: 50px;
}

.logo-img {
  height: 100%;
}

.header-nav {
  margin-right: 50px;
}

.header-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.header-list :not(:first-child) {
  margin-left: 20px;
}

.header-link {
  color: #000;
  text-transform: uppercase;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: var(--header-height);
  text-decoration: none;
  display: inline-block;
}

.header-link:hover {
  color: var(--hover-color);
}

.is-active {
  color: var(--hover-color);
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--hover-color);
  border-radius: 8px;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

